/*& PORTFOLIO */

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  padding: 0 100px;
}

.inner {
  width: 100%;
  max-width: var(--max-size);
}

@media only screen and (max-width: 850px) {
  .container {
    padding: 20px;
  }
}

.feed {
  display: flex;
  flex-direction: column;
  max-width: 1600px;
  gap: 60px;
}
